type DataReturn = LookupResponse;

interface IPDataResp {
  data: DataReturn;
  hasFailed: boolean;
}

export const getLocation = (): Promise<IPDataResp> => {
  const apiKey = process.env.IPDATA_API_KEY;

  const ipData = fetch(`https://api.ipdata.co/?api-key=${apiKey}`, {
    method: "GET",
  }).then((resp) =>
    resp.json().then(
      (data): IPDataResp => {
        if (!resp.ok) {
          Sentry.captureException(new Error(`${resp.status} ${data}`));
          return {
            data,
            hasFailed: true,
          };
        }
        return {
          data,
          hasFailed: false,
        };
      },
    ),
  );

  return ipData;
};

const CCPARegions = ["CA"];

export const isCCPALocation = (locationData: LookupResponse): boolean => {
  const region = locationData.region_code;
  if (!region) return true;

  return CCPARegions.includes(region);
};

interface LookupResponse {
  ip: string;
  is_eu: boolean;
  city?: string;
  region?: string;
  region_code?: string;
  country_name: string;
  country_code: string;
  continent_name: string;
  continent_code: string;
  latitude: number;
  longitude: number;
  postal?: string;
  calling_code: string;
  flag: string;
  emoji_flag: string;
  emoji_unicode: string;
  asn: {
    asn: string;
    name: string;
    domain: string;
    route: string;
    type: string;
  };
  languages: { name: string; native: string }[];
  currency: {
    name: string;
    code: string;
    symbol: string;
    native: string;
    plural: string;
  };
  time_zone: {
    name: string;
    abbr: string;
    offset: string;
    is_dst: boolean;
    current_time: string;
  };
  threat: {
    is_tor: boolean;
    is_proxy: boolean;
    is_anonymous: boolean;
    is_known_attacker: boolean;
    is_known_abuser: boolean;
    is_threat: boolean;
    is_bogon: boolean;
  };
  count: number;
  status: number;
}
