import {
  ShowBannerAction,
  HideBannerAction,
  TrackBannerCheckAction,
  LayoutWorkflow,
} from "contexts/Layout";
import shouldForceCCPABannerOpen from "./shouldForceCCPABannerOpen";
import Cookies from "js-cookie";
import {
  getBannerInteraction,
  loadAnalytics,
  setTrackingPreferenceCookies,
} from "utils/analytics";
import { getLocation, isCCPALocation } from "utils/getLocation";
import { getCookiesDomain } from "utils/getCookiesDomain";

export const runCCPA: LayoutWorkflow = (dispatch, { showCCPABanner }) => {
  // We want to track this checkCCPA has run once per session
  dispatch(new TrackBannerCheckAction());
  const domain = getCookiesDomain();

  if (!showCCPABanner) {
    const bannerInteraction = getBannerInteraction();

    if (bannerInteraction === "noPreviousInteraction") {
      Cookies.remove("mutiny.user.token", {
        path: "/",
        domain,
      });
    }

    if (
      bannerInteraction === "softAccept" ||
      bannerInteraction === "noPreviousInteraction"
    ) {
      getLocation().then((res) => {
        if (res.hasFailed || isCCPALocation(res.data)) {
          // If IPdata fails show banner to all visitors
          dispatch(new ShowBannerAction());
        } else {
          setTrackingPreferenceCookies("softAccept");
        }
      });
    } else {
      const trackingPref =
        bannerInteraction === "explicityAccepted" ? "accepted" : "rejected";

      loadAnalytics(dispatch, trackingPref);
    }
  }

  const result = shouldForceCCPABannerOpen(window.location.search);

  if (result) dispatch(new ShowBannerAction());
};

export const rejectCookies: LayoutWorkflow = (dispatch) => {
  loadAnalytics(dispatch, "rejected");
  setTrackingPreferenceCookies("explicitlyDenied");

  dispatch(new HideBannerAction());
};

export const acceptCookies: LayoutWorkflow = (dispatch) => {
  const domain = getCookiesDomain();
  loadAnalytics(dispatch, "accepted");
  setTrackingPreferenceCookies("explicitlyAccepted");

  Cookies.set("mutiny.user.token", localStorage.getItem("mutiny.user.token"), {
    path: "/",
    domain,
  });

  dispatch(new HideBannerAction());
};
